  /*MovieCard*/
  .moviecard {
    width: 210px;
    height: 380px;
    margin: .2rem;
  
    position: relative;
    overflow: hidden;
    border: none;
  
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
    box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  }
  

  /*Rating*/
  .rating {
    position: absolute;
    padding: 6px;
    width: 10%;
    top: 0;
    color: #eeeae6;
    background-color: #19233b71;
    text-align: center;
  }
  
  
  /*Poster dimension*/
  .poster{
    width: 100%;
    height: 100%;
    color: #eeeae6;
  }
  
  /*Poster img*/
  .poster img {
    height: 80%;
    width: 100%;
  }
  /*Poster img on Hover*/
  .poster:hover img {
    box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
    transform: scale(1.05, 1.05);
    opacity: 0.3;
  }


  /*Overview*/
  .poster p {
    font-family: "Helvetica Neue", sans-serif;
    position: absolute;
    text-align: center;
    padding: 1.5rem .2rem 0 .2rem;
    opacity: 0;
  }
  /*Overview on Hover*/
  .poster:hover p {
    position: absolute;
    opacity: 1;
    z-index: 1;
    height: 74%;
    overflow-y: auto;
  }
  

  /*Details*/
  .details {
    z-index: 2;
    background-color: #19233b;
    text-align: center;
  
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    height: 20%;
  }
  
  
  /*Title*/
  .details a{
    color: #eeeae6;
    text-decoration: none;
  }
  .details p {
    font-family: "Helvetica Neue", sans-serif;
    text-transform: uppercase;
    font-size: .8rem;
    letter-spacing: .05rem;
    line-height: 1rem;
    color: #eeeae6;
    padding: 2px 2px 0 2px;
    margin-top: 13px;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
  
  /*Bottom h3*/
  .details h3 {
    margin-top: 5px;
    font-family: "Helvetica Neue", serif;
    color: #eeeae6;
  }
  

 