@import url("https://fonts.googleapis.com/css?family=Helvetica+Neue:100,300,400,700");
@import url("https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i");


* {
    margin: 0;
    border: 0;
  }

body {
    font-family: var(--font-helvetica);
    background-color: #141927;
  }
  
.home {
  background-color: var(--main-color);
  padding: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

/*Page Heading*/
h1 {
  font-size: 3rem;
  letter-spacing: 0.9px;
  background: #eeeae6;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: fit-content;
}

/*Search Bar*/
.search {
  width: 60%;
  margin: 4rem 0 2rem;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 1.5rem 1.75rem;
  border-radius: 3rem;
  background: #19233b;
  -webkit-box-shadow: 5px 5px 7px #151e31, -5px -5px 7px #151e31;
  box-shadow: 5px 5px 7px #151e31, -5px -5px 7px #151e31;
}
  
/*Inside of search bar*/
.search input {
  width: 90%;
  border: none;
  font-size: 1.5rem;
  font-family: var(--font-helvetica);
  font-weight: 500;
  padding-right: 1rem;

  outline: none;
  color: #eeeae6;
  background: #19233b;
}
  
/*Search Bar Magnifier*/
.search img {
  width: 35px;
  height: 35px;
  cursor: pointer;
  color: #141927;
}

