/*container*/
.container {
  box-sizing: border-box;
  display: block;
  font-size: 16px;
  line-height: 23px;
  
}
  
/*container/grid*/
.grid{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 80%;
  margin: 0 auto;
  margin-top: 2rem;
  column-gap: 5px;
  justify-content: center;
  flex-wrap: wrap;
}

/*container/empty*/
.empty {
  width: 100%;
  margin-top: 3rem;

  display: flex;
  justify-content: center;
  align-items: center;
}
/*Empty Search*/
.empty h2 {
  font-size: 1.25rem;
  color: #eeeae6;
  font-family: var(--font-helvetica);
}

/*TMDB Attribution*/
.tmdb{
  color: #eeeae6;
  padding: 2rem;
}
.tmdb img{
  width: 1.2rem;
  margin-left: .2rem;
}


/*Custom Scrolbar*/
::-webkit-scrollbar {
  width: 13px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #394870;
}
/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #19233b;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #141927;
}

@media screen and (max-width: 1024px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 680px) {
  .home {
    padding: 4rem 2rem;
  }
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .search {
    padding: 1rem 1.75rem;
  }

  .search input {
    font-size: 1rem;
  }

  .search img {
    width: 20px;
    height: 20px;
  }
}

@media screen and (max-width: 425px) {
  .home {
    padding: 4rem 1rem;
  }

  h1 {
    font-size: 2rem;
  }

  .container {
    margin-top: 2rem;
  }
  .grid {
    grid-template-columns: repeat(1, 1fr);
  }
  .movie {
    width: "100%";
    margin: 1rem;
  }
}

